<template>
  <div class="ebook-reader">
    <div id="read"></div>
  </div>
</template>

<script>
  //  mapGetters  vuex 
  // import { mapGetters } from 'vuex'
  //  mixin.js  mapGetter mapActionsmixin.js  mapGetters
  import { ebookMixin } from '../../utils/mixin'
  //  mapActions  mapGetters mapActionsvuex  actios  actions.js 
  //  mapGetters  mixin.js 
  // import { mapActions } from 'vuex'
  //  Epubjs
  import Epub from 'epubjs'
  global.ePub = Epub
  export default {
    //  mixins  mixin.js 
    mixins: [ebookMixin],
    methods: {
      //  mapActions  actios.js ,  this.xx() xx
      //  mapGetters  mixin.js 
      // ...mapActions(['setMenuVisible']),
      // 
      prevPage() {
      //  
        if (this.rendition) {
          this.rendition.prev()
          //  hideTitleAndMenu 
          this.hideTitleAndMenu()
        }
      },
      // 
      nextPage() {
        if (this.rendition) {
          this.rendition.next()
          this.hideTitleAndMenu()
        }
      },
      // , $store.dispatch vuex  setMenuVisible mapActions 
      //  actions  actions.js  mixin.js  mixin.js 
      toggleTitleAndMenu() {
        this.setMenuVisible(!this.menuVisible)
        //  toggleTitleAndMenu  menu  SettingFont  SetSettingVisible
        if (this.menuVisible) {
          this.setSettingVisible(-1)
        }
        this.setFontFamilyVisible(false)
      },
      hideTitleAndMenu() {
        this.setMenuVisible(false)
        //  EbookSettingFont
        this.setSettingVisible(-1)
        //   font-family 
        this.setFontFamilyVisible(false)
      },
      initEpub() {
        //  Nginx  8081
        const url = 'http://192.168.31.133:8081/epub/' + this.fileName + '.epub'
        // console.log(url);
        this.book = new Epub(url)
        this.setCurrentBook(this.book)
        this.rendition = this.book.renderTo('read', {
          width: innerWidth,
          height: innerHeight,
          //  
          method: 'default'
        })
        // 
        this.rendition.display()
        //  touchstart   touchend
        //  timeStampchangedTouches  
        //  clientX 
        this.rendition.on('touchstart', event => {
          this.touchstartX = event.changedTouches[0].clientX
          this.touchstartTime = event.timeStamp
        })
        this.rendition.on('touchend', event => {
          const offsetX = event.changedTouches[0].clientX - this.touchstartX
          const time = event.timeStamp - this.touchstartTime
          // console.log(offsetX, time);
          //  500  40
          if (time < 500 && offsetX > 40) {
            this.prevPage()
          } else if (time < 500 && offsetX < -40) {
            this.nextPage()
          } else {
            this.toggleTitleAndMenu()
          }
          // 
          event.preventDefault()
          event.stopPropagation()
        })
        this.rendition.hooks.content.register(content => {
          Promise.all([
            content.addStylesheet('http://192.168.31.133:8081/fonts/daysOne.css'),
            content.addStylesheet('http://192.168.31.133:8081/fonts/cabin.css'),
            content.addStylesheet('http://192.168.31.133:8081/fonts/montserrat.css'),
            content.addStylesheet('http://192.168.31.133:8081/fonts/tangerine.css')
          ]).then(() => {})
        })
      }
    },
    mounted() {
      //  mounted 
      // const fileName = this.$route.params.fileName
      // console.log(fileName);
      // split  xxx 
      // join  xxx 
      // const baseUrl = 'http://192.168.31.133:8081/epub/'
      // const fileName = this.$route.params.fileName.split('|').join('/')
      this.setFileName(this.$route.params.fileName.split('|').join('/')).then(() => {
        this.initEpub()
      })
    }
  }
</script>

